@charset "utf-8";

* {

    -webkit-box-sizing: border-box;

    box-sizing: border-box;

}

/*禁止文本缩放 字体设置  取消touch高亮效果*/

html {

    width: 100%;

    height: 100%;

    margin: 0px;

    padding: 0px;

    -webkit-text-size-adjust: 100%;

    font-family: -apple-system, "PingFang SC", "Helvetica Neue", Helvetica, STHeiTi, sans-serif;

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

    font-size: 62.5%;

    background: transparent;

}

body {

    width: 100%;

    height: 100%;

    -webkit-overflow-scrolling: touch;

    background-color: #eceff1;

    color: #333333;

    font-size: 14px;

}

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, sumary {

    margin: 0;

    padding: 0;

}

/*清除输入框内阴影*/

input, select, textarea {

    border: 0;

    -webkit-appearance: none;

    -moz-appearance: none;

         appearance: none;

}

ol, ul {

    list-style: none;

}

/*禁止选中文本内容*/

*:not(input, select, textArea) {

    -webkit-user-select: none;

}

/*禁用长按页面时的弹出菜单(iOS下有效) ,img和a标签都要加*/

img, a {

    -webkit-touch-callout: none;

}

a {
    diaplay: block;
    cursor: pointer;
}

a, a:active, a:hover {

    text-decoration: none;

}

/*去掉点击链接和文本框对象时默认的灰色半透明覆盖层(iOS)或者虚框(Android)*/

a, button, input, textarea {

    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

}

.clearfix {
    *zoom: 1;
}

.clearfix:before,
.clearfix:after {
    display: table;
    line-height: 0;
    content: "";
}

.clearfix:after {
    clear: both;
}

.pull-left {
    float: left;
}

.pull-right {
    float: right;
}


